.details-page {
  padding: 5vh 5vw;
  min-height: 60vh;
}

.details-section {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 50px 0px;
}

.details-page__country-flag {
  width: 500px;
  max-height: 300px;
}

.details-page__back-button {
  padding: 20px;
  font-size: 1.2rem;
  box-shadow: 0px 0px 5px $Dark;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  border-radius: 5px;
}

@media (min-width: 1250px) {
  .details-section {
    display: flex;
    justify-content: flex-start;
  }
}